import React from "react";

export default class LoginForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      username: {
        value: "",
        error: ""
      },
      password: {
        value: "",
        error: ""
      }
    };
  }
  fieldMeta = {
    username: {
      rules: [{ require: true }]
    },

    password: {
      rules: [
        {
          minLength: 3
        }
      ]
    }
  };

  onInputChange = (e) => {
    const { value, name } = e.target;
    // 获取校验结果
    const error = this.doValidate(value, name);

    this.setState({
      [name]: {
        value,
        error
      }
    });
  };

  doValidate = (value, name) => {
    // 读取校验规则
    const rules = this.fieldMeta[name].rules;
    return this.validator(value, rules);
  };

  validator = (value, rules = []) => {
    console.log(rules);
    for (let i = 0; i < rules.length; i++) {
      let rule = rules[i];
      if (rule.require && !value) {
        return "字段不能为空";
      }
      if (rule.minLength !== undefined && value.length < rule.minLength) {
        return "字段长度不能少于3";
      }
    }
    return "";
  };

  onSubmit = () => {
    // 校验所有表单
    Object.keys(this.state).forEach((item) => {
      const value = this.state[item].value;
      const error = this.doValidate(value, item);
      this.setState({
        [item]: {
          value,
          error
        }
      });
    });
  };

  render() {
    return (
      <div>
        <div>
          <label htmlFor="">username: </label>
          <input
            type="text"
            name="username"
            placeholder="username"
            value={this.state.username.value}
            onChange={this.onInputChange}
          />
        </div>
        <div>{this.state.username.error}</div>
        <div>
          <label htmlFor="">password: </label>
          <input
            type="text"
            name="password"
            placeholder="password"
            value={this.state.password.value}
            onChange={this.onInputChange}
          />
        </div>
        <div>{this.state.password.error}</div>
        <button onClick={this.onSubmit}>登录</button>
      </div>
    );
  }
}
